.containerFood{
  // background-image: url('../images/menusFood.jpg');
  // background-repeat: no-repeat;
  // background-size: cover;
  position: relative;
  z-index: 3;
  display: grid;
  grid-template-columns: 20% 20% 20% 20% 20%;
  grid-template-rows: 40% 30% 30%;
  .item{
      display: flex;
      justify-content: center;
      align-items: center;
      img{
          max-width: 100%;
          max-height: 100%;
      }
    .maskContainerFood{
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      opacity: 0;
      z-index: 1;
      &:hover{
        opacity: .8;
      }
      h1{
        font-size: 1.5em;
        color: #fff;
      }
    }
  }
  .itemBeer{
    background-image: url('../images/beer_menu.png');
    background-repeat: no-repeat;
    background-size: cover;
    grid-column-start: 1;
    grid-column-end: span 2;
    grid-row-start: 1;
    grid-row-end: span 3;
    position: relative;
  }
  .itemMenu{
    background-image: url('../images/menu.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    grid-column-start: 3;
    grid-column-end: span 2;
    grid-row-start: 1;
    grid-row-end: span 1;
    position: relative;
  }
