  //menus
  .containerMenu{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    &.active{
      z-index:4;
    }
    .imgMenu,
    .menu{
      height: 100%;
      width: 50%;
      transition: .5s;
        @include mq($mobile){
            height: 50%;
            width: 100%;
        }
    }
    .imgMenu{
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      transform: translateX(-100%);
      &.active{
        transform: translateX(0);
      }
    }
    .menu{
      background-size: 100% 100%;
      background-repeat: no-repeat;
      transform: translateX(100%);
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      &.active{
        transform: translateX(0);
      }
      .titleAnimate{
          margin: 1em 0;
      }
      img{
          max-width: 90%;
          max-height: 90%;
      }
      .feather{
        position: absolute;
        top: 8%;
        right: 2%;
        color: $gold;
      }
    }
  }
